import { createElement } from 'react';
import { elementAttr } from "../../../utils/template-helpers/element-attr";
import './styles';
export const Icon = ({ element }) => {
    const { settings } = element;
    const actionEnabled = settings.action?.actionType !== 'no';
    const tagName = actionEnabled ? 'a' : 'div';
    const classLink = actionEnabled ? 'xb-link' : '';
    function getIcon() {
        if (!settings.icon.source) {
            return '<div class="xb-placeholder-select-data">Please select an icon.</div>';
        }
        if (settings.icon.type === 'image') {
            return `<img class="xb-image" src="${settings.icon.source}" alt="${settings.icon.name}" draggable="false" />`;
        }
        else {
            return settings.icon.source;
        }
    }
    return createElement(tagName, {
        ...elementAttr(element, {
            classNames: ['xb-icon', classLink, settings.static?.class, settings?.modifier?.handle],
        }),
        dangerouslySetInnerHTML: { __html: getIcon() },
    });
};
